.contact-form {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 35rem;
  margin: auto;
}

.contact-form__text-input {
  border-radius: 0;
  border: solid;
  border-style: solid;
  border-width: 1px;
  border-color: #c5c5c5;

  min-width: 12rem;

  padding: 0.5rem;
  background-color: white;
}

.contact-form__text-input:focus {
  outline: none;
  border-color: #257AFD;
  box-shadow: 0 0 5px #257AFD;
}

.contact-form__input-title {
  color: #bbbbbb;
}

.contact-form__input-title_invalid {
  font-size: 0.9em;
  margin: 0.5rem 0 -0.2rem 0;
  color: rgb(228, 51, 51);
}

.contact-form__text-input.form-message {
  padding: 0.5rem;
  height: 20rem;
  resize: none;
}

.contact-form__text-input_invalid {
  border-color: #ff0000;
}

.contact-form__submit-container {
  height: 5rem;
  display: flex;
}

.contact-form__submit {
  display: inline-block;
  cursor: pointer;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: auto;
  padding: 0.5rem 2rem;
}

.contact-form__input-title.form-message-status {
  display: inline-block;
  padding-left: 1rem;
}


