.close-button {
  position: absolute;
  top: 0em;
  right: 0em;
  font-size: 2em;
  width: 1.1rem;
  padding: 1rem;
  opacity: 0.8;
  color: #3b3b3b;
}

.close-button:hover {
  opacity: 1;
  cursor: pointer;
}

.close-button:active {
  transform: translateY(1px);
  will-change: transform;
}